import {
    FETCH_PROMO_CODES,
    FETCH_PROMO_CODES_SUCCESS,
    FETCH_PROMO_CODES_ERROR,
    UPDATE_PROMO_CODES,
    UPDATE_PROMO_CODES_SUCCESS,
    UPDATE_PROMO_CODES_ERROR,
    ADD_PROMO_CODES,
    ADD_PROMO_CODES_SUCCESS,
    ADD_PROMO_CODES_ERROR,
    DELETE_PROMO_CODES,
    DELETE_PROMO_CODES_SUCCESS,
    DELETE_PROMO_CODES_ERROR,
    PROMO_CODES_ERROR_REMOVE
} from '@redux/types'

import ApiRequest from '@utils/ApiRequest'
import qs from 'qs'

export const getPromoCodes = (data = {}) => {
    return (dispatch) => {
        dispatch({
            type: FETCH_PROMO_CODES,
            error: null
        })

        const variables = qs.stringify(data);
        ApiRequest.get(`admin/promo_codes?${variables}`, data)
            .then((response) => {

                if(response.error || response.errors)
                {
                    dispatch({
                        type: FETCH_PROMO_CODES_ERROR,
                        error: response.errors,
                        sortBy: data.sortBy,
                        sortDir: data.sortDir,
                        filters: data.filters
                    })
                }else{
                    dispatch({
                        type: FETCH_PROMO_CODES_SUCCESS,
                        list: response,
                        sortBy: data.sortBy,
                        sortDir: data.sortDir,
                        filters: data.filters
                    })
                }
            })
    }
};

export const setError = (type = PROMO_CODES_ERROR_REMOVE, error = null) => {
    return (dispatch) => {
        dispatch({
            type,
            error
        })
    }
};

export const updatePromoCode = (data, successCallback) => {
    return (dispatch) => {
        dispatch({
            type: UPDATE_PROMO_CODES,
            error: null
        })

        ApiRequest.put('admin/promo_codes/'+data._id, data)
            .then((response) => {

                if(response.error || response.errors)
                {
                    dispatch({
                        type: UPDATE_PROMO_CODES_ERROR,
                        error: response.errors
                    })
                }else{
                    dispatch({
                        type: UPDATE_PROMO_CODES_SUCCESS,
                        payload: response
                    })

                    if(successCallback)
                    {
                        successCallback()
                    }
                }
            })
    }
};

export const createPromoCode = (data, successCallback) => {
    return (dispatch) => {
        dispatch({
            type: ADD_PROMO_CODES,
            error: null
        })

        ApiRequest.post('admin/promo_codes', data)
            .then((response) => {

                if(response.error || response.errors)
                {
                    dispatch({
                        type: ADD_PROMO_CODES_ERROR,
                        error: response.errors
                    })
                }else{
                    dispatch({
                        type: ADD_PROMO_CODES_SUCCESS,
                        payload: response
                    })

                    if(successCallback)
                    {
                        successCallback()
                    }
                }
            })
    }
};

export const deletePromoCode = (id, successCallback) => {
    return (dispatch) => {
        dispatch({
            type: DELETE_PROMO_CODES,
            error: null
        })

        ApiRequest.delete('admin/promo_codes/'+id)
            .then((response) => {

                if(response.error || response.errors)
                {
                    dispatch({
                        type: DELETE_PROMO_CODES_ERROR,
                        error: response.errors
                    })
                }else{
                    dispatch({
                        type: DELETE_PROMO_CODES_SUCCESS,
                        payload: response
                    })

                    if(successCallback)
                    {
                        successCallback()
                    }
                }
            })
    }
};

