import {
    FETCH_USERS,
    FETCH_USERS_SUCCESS,
    FETCH_USERS_ERROR,
    UPDATE_USERS,
    UPDATE_USERS_SUCCESS,
    UPDATE_USERS_ERROR,
    ADD_USERS,
    ADD_USERS_SUCCESS,
    ADD_USERS_ERROR,
    DELETE_USERS,
    DELETE_USERS_SUCCESS,
    DELETE_USERS_ERROR,
    USERS_ERROR_REMOVE
} from '@redux/types'

import ApiRequest from '@utils/ApiRequest'

export const getUsers = (data = {}) => {
    return (dispatch) => {
        dispatch({
            type: FETCH_USERS,
            error: null
        })

        ApiRequest.get('admin/users?page='+data.page, data)
            .then((response) => {

                if(response.error || response.errors)
                {
                    dispatch({
                        type: FETCH_USERS_ERROR,
                        error: response.errors
                    })
                }else{
                    dispatch({
                        type: FETCH_USERS_SUCCESS,
                        list: response
                    })
                }
            })
    }
};

export const setError = (type = USERS_ERROR_REMOVE, error = null) => {
    return (dispatch) => {
        dispatch({
            type,
            error
        })
    }
};

export const updateUser = (data, successCallback) => {
    return (dispatch) => {
        dispatch({
            type: UPDATE_USERS,
            error: null
        })

        ApiRequest.put('admin/users'+data._id, data)
            .then((response) => {

                if(response.error || response.errors)
                {
                    dispatch({
                        type: UPDATE_USERS_ERROR,
                        error: response.errors
                    })
                }else{
                    dispatch({
                        type: UPDATE_USERS_SUCCESS,
                    })

                    if(successCallback)
                    {
                        successCallback()
                    }
                }
            })
    }
};

export const createUser = (data, successCallback) => {
    return (dispatch) => {
        dispatch({
            type: ADD_USERS,
            error: null
        })

        ApiRequest.post('admin/users', data)
            .then((response) => {

                if(response.error || response.errors)
                {
                    dispatch({
                        type: ADD_USERS_ERROR,
                        error: response.errors
                    })
                }else{
                    dispatch({
                        type: ADD_USERS_SUCCESS,
                    })

                    if(successCallback)
                    {
                        successCallback()
                    }
                }
            })
    }
};

export const deleteUser = (userId, successCallback) => {
    return (dispatch) => {
        dispatch({
            type: DELETE_USERS,
            error: null
        })

        ApiRequest.delete('admin/users', userId)
            .then((response) => {

                if(response.error || response.errors)
                {
                    dispatch({
                        type: DELETE_USERS_ERROR,
                        error: response.errors
                    })
                }else{
                    dispatch({
                        type: DELETE_USERS_SUCCESS,
                    })

                    if(successCallback)
                    {
                        successCallback()
                    }
                }
            })
    }
};

