import React from 'react'
import _ from "lodash";
import {Alert} from 'reactstrap';

export default function Errors(props)
{
    if(!props.errors)
    {
        return null;
    }

    let keys = _.isString(props.errors)?null:Object.keys(props.errors)
    let errors = []

    if(keys && keys.length)
    {
        keys.map((item) => {
            if(Array.isArray(props.errors[item])){
                for(let i in props.errors[item]){
                    errors.push(
                        <div key={item+i}>
                            {props.errors[item][i]}
                        </div>
                    )

                }
            }
        })
    }else if(_.isString(props.errors)){
        errors.push(
            <div key={1}>
            {props.errors}
            </div>
        )
    }
    return (
        <div key={1} className="alert alert-danger" role="alert">
            {errors}
        </div>
    )
}
