import React from 'react'
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';

export default class AdminModal extends React.Component{

    constructor(props){
        super(props)

        this.state = {
            modal: props.open
        }
    }

    onSubmit = () => {
        if(this.props.onSubmitCallBack)
        {
            this.props.onSubmitCallBack()
        }else {
            this.toggle()
        }
    }

    toggle(){
        this.setState(prevState => ({
            modal: !prevState.modal
        }));
    }

    onClose = () =>  {

        if(this.props.closeCallBack)
        {
            this.props.closeCallBack()
        }else {
            this.toggle()
        }
    }

    render(){
        return (
            <Modal isOpen={this.state.modal} toggle={this.onClose} className={this.props.className}>
                <ModalHeader toggle={this.onClose}>{this.props.title}</ModalHeader>
                <ModalBody>
                    {this.props.children}
                </ModalBody>
                <ModalFooter>
                    {this.props.footer}
                </ModalFooter>
            </Modal>
        )
    }
}
